  handledob = (date) => {
    this.setState({
      dob: date
    })
  }

  updateuser = (e) => {
    e.preventDefault()
    this.setState({ saveButton: 'Saving ...' })
    const {
      dob,
      language,
      secondarymobile,
      secondaryemail,
      pwdtime,
      theme,
      address1,
      address2,
      postalCode,
      city,
      state,
      country
    } = this.state
    let user = JSON.parse(localStorage.getItem('logInUserData'))
    user.dob = dob
    user.pwdtime = pwdtime
    user.theme = theme
    user.secondarymobile = secondarymobile
    user.secondaryemail = secondaryemail
    user.language = language
    const add = {
      address1,
      address2,
      postalCode,
      city,
      state,
      country
    }
    user.address = add
    localStorage.setItem('logInUserData', JSON.stringify(user))
    localStorage.setItem('theme', theme)
    axios
      .post('/backendapi/adddetails', user)
      .then((res) => {
        this.setState({ saveButton: 'Save Changes' })
        window.location.reload()
      })
      .catch()
  }
